<template>
	<view class="page">
		<tab class="sticky" ref="tab" @tabChange="onTabChange" />
		<article-list :data="articles" />
	</view>
</template>

<script>
	import api from '@/http/ApiService.js'
	import utils from '@/utils/Utils.js'
	var self
	export default {
		data() {
			return {
				page: 1,
				cid: 0,
				enableLoadMore: true,
				articles: [],
				scrollTop: 0
			}
		},
		onLoad() {
			self = this
			self.getProjectTree()
		},
		onPullDownRefresh() {
			self.getProjectTree()
		},
		onReachBottom() {
			if (self.enableLoadMore) self.getProjectArticle()
		},
		methods: {
			getProjectTree() {
				api.getProjectTree().then(res => {
					res.forEach((tab) => {
						tab.name = utils.formatHtml(tab.name)
					})
					self.$refs.tab.setTabs(res)
					if (res.length > 0) {
						self.cid = res[0].id
						self.getProjectArticle()
					}
				})
			},
			getProjectArticle() {
				if (self.cid != 0) {
					api.getProjectArticle(self.page, self.cid).then(res => {
						uni.stopPullDownRefresh()
						// 判断是否可以继续加载下一页
						res.curPage == 1 ? self.articles = res.datas :
							self.articles = self.articles.concat(res.datas)
						self.enableLoadMore = res.over == false
						self.page++
						uni.pageScrollTo({
							scrollTop:0,
							duration:200
						})
					})
				}
			},
			onTabChange(id) {
				self.cid = id
				self.page = 1
				self.getProjectArticle()
			}
		}
	}
</script>

<style>
	.sticky {
		display: flex;
		flex-direction: column;
		position: sticky;
		top: var(--window-top);
		z-index: 999;
	}
</style>
